import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { redirectToMain, purchaseBooks } from '../../redux/actions';
import './Cart.scss';

const Cart = () => {
  const { cart, user } = useSelector((state) => state);
  const dispatch = useDispatch();

  return (
    <div className="cart-container">
      <div className="cart">
        <div className="cart-controls">
          <button
            className="cart_to-catalog_button"
            type="button"
            onClick={() => {
              dispatch(redirectToMain());
            }}
          >
            Back To Catalog
          </button>
          <button
            className="cart_purchase-button"
            type="button"
            onClick={() => {
              dispatch(purchaseBooks(user.token, cart.books));
            }}
          >
            Purchase
          </button>
        </div>
        <div>
          <table className="table">
            <thead>
              <tr>
                <th>Name</th>
                <th>Count</th>
                <th>Price</th>
                <th>Total</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>Eloquent Javascript</td>
                <td>2</td>
                <td>15.38</td>
                <td>30.76</td>
              </tr>
            </tbody>
          </table>
        </div>
        <div className="cart-cart_total-price">
          <p>{`Total Price: ${'100'} $`}</p>
        </div>
      </div>
    </div>
  );
};

export default Cart;
